import React from "react";

export default function Footer() {
    return (
        <footer className="glass-box bottom-bar white-text">
            <p className="copyright">&#169; 2021 Merlijn Sevenhuijsen</p>
            <ul>
                <li>
                    <a
                        className="flex-around"
                        href="https://www.facebook.com/merlijn.sevenhuijsen/"
                    >
                        <img
                            className="footer-links"
                            src={require("url:./Images/Links/facebook.png")}
                            alt="Facebook link"
                        />
                    </a>
                </li>
                <li>
                    <a
                        className="flex-around"
                        href="https://twitter.com/merlijndussen/"
                    >
                        <img
                            className="footer-links"
                            src={require("url:./Images/Links/twitter.png")}
                            alt="Twitter link"
                        />
                    </a>
                </li>
                <li>
                    <a
                        className="flex-around"
                        href="https://www.instagram.com/merlijndussen/"
                    >
                        <img
                            className="footer-links"
                            src={require("url:./Images/Links/instagram.png")}
                            alt="Instagram link"
                        />
                    </a>
                </li>
                <li>
                    <a
                        className="flex-around gitH"
                        href="https://github.com/SevenhuijsenM"
                    >
                        <img
                            className="footer-links"
                            src={require("url:./Images/Links/github.png")}
                            alt="Github link"
                        />
                    </a>
                </li>
            </ul>
        </footer>
    );
}
