import React from "react";

function getRandomColor() {
    var letters = "0123456789ABCDEF";
    var color = "#";
    for (var i = 0; i < 6; i++) {
        color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
}

export default function SingleProject({ currentProject }) {
    // Define the color of the project background
    const LinearGradient = `linear-gradient(315deg, ${getRandomColor()}, ${getRandomColor()})`;
    return (
        <div className="project-box background-glass-box content-card hovering-span-cover">
            <span
                style={{
                    background: LinearGradient,
                }}
                className="background-glass-box-child"
            />
            <span
                style={{
                    background: LinearGradient,
                }}
                className="background-glass-box-child"
            ></span>
            <span className="hovering-span" />
            <div className="glass-box">
                <h2>{currentProject[0]}</h2>
                <h4>{currentProject[1]}</h4>
                {currentProject[2] ? (
                    <img
                        className="project-image"
                        alt={currentProject[2][0]}
                        src={currentProject[2][1]}
                    ></img>
                ) : null}
                {currentProject[3] ? (
                    <a className="general-button" href={currentProject[3][0]}>
                        {currentProject[3][1]}
                    </a>
                ) : null}
            </div>
        </div>
    );
}
