import React from "react";
import SingleProject from "./Project";
export default function Projects() {
    let MyProjects = [
        [
            "Viseye",
            "An interactive tool to visualize eye-tracking data. Made by a group of 6 students within 8 weeks.",
            [
                "Viseye figure",
                require("url:./Images/Projects/viseye-figure.png"),
            ],
            ["https://viseye.web.app", "Go to project"],
        ],
        [
            "Rocket game",
            "A game resolved around flying a rocket to the end destination. This project was done to learn more about creating games as well as applying the gained programming knowledge",
        ],
        [
            "Website",
            "Create a website with limited experience, include multiple different pages with different effects. Get to know more about the prcoess of creating websites from scratch. The website you are currently looking at is the final result.",
        ],
        [
            "Process Mining",
            "A tool to analyze the data from a BPI_Challenge, which requires a dataset to train on and uses artificial intelligence to make predictions based on the trained data. Both the next event and time until the next event were predicted, and the tool had an accuracy of 75% on the prediction of the next event.",
        ],
    ];

    return (
        <div className="projects container" id="Projects">
            {MyProjects.map(createProject)}
        </div>
    );

    function createProject(project) {
        return <SingleProject key={project[0]} currentProject={project} />;
    }
}
