import React, { Component } from "react";
import { init, send } from "emailjs-com";
init("user_WA8HxE5tnSC9ye10AgzJI");

class Contact extends Component {
    constructor() {
        super();

        // Define the state of this component
        this.state = {
            name: "",
            subject: "",
            email: "",
            message: "",
            mailSent: false,
            error: null,
            loading: false,
        };
    }

    handleFormSubmit(event) {
        event.preventDefault();

        // Check if the email, the message, the name and the subject are valid
        if (
            this.validateNameAndSubject() &&
            this.validateEmail() &&
            this.validateMessage()
        ) {
            // Set the state of the error message back to null
            // Also set loading to true
            this.setState({ error: null, loading: true });

            // The template that will be sent to the email service
            var templateParams = {
                name: this.state.name,
                subject: this.state.subject,
                email: this.state.email,
                message: this.state.message,
            };

            // Make a copy of the this to set the state ( context in send is different)
            let contactForm = this;

            // Send an email
            send("service_3m23okx", "template_fgurq98", templateParams).then(
                function () {
                    // Set the mail state to true
                    contactForm.setState({
                        mailSent: true,
                        loading: false,
                        name: "",
                        subject: "",
                        email: "",
                        message: "",
                    });
                },
                function () {
                    // Set the mail state to true
                    contactForm.setState({
                        mailSent: false,
                        loading: false,
                        error:
                            "There was an error that occured whilst sending the email.",
                    });
                }
            );
        } else {
            // Set the state to no email sent
            this.setState({ mailSent: false });
        }
    }

    validateEmail() {
        // The email length is 0
        if (this.state.email.length == 0) {
            this.setState({ error: "The e-mail address is empty." });
            return false;
        } else if (
            // The email does not match the format of an email address
            !this.state.email.match(
                /^[A-Za-z._\-[0-9]*[@][A-Za-z]*[.][a-z]{2,4}$/
            )
        ) {
            this.setState({ error: "The e-mail address is invalid." });
            return false;
        }

        // Set the form address to valid otherwise
        return true;
    }

    validateMessage() {
        // Define the minimum amount of characters in the message container
        var requiredCharacters = 30;

        // The message does not contain at least 30 characters
        if (this.state.message.length - requiredCharacters <= 0) {
            this.setState({
                error: "The message must contain at least 30 characters.",
            });
            return false;
        }

        // Otherwise the message is valid.
        return true;
    }

    validateNameAndSubject() {
        // The name needs to have at least 1 character
        if (this.state.name == 0) {
            this.setState({
                error: "The name must contain at least 1 character.",
            });
            return false;
        }

        // The subject needs to have at least 1 character
        if (this.state.subject == 0) {
            this.setState({
                error: "The subject must contain at least 1 character.",
            });
            return false;
        }

        // Otherwise accept the name and subject
        return true;
    }

    render() {
        return (
            <div className="container contact-section" id="Contact">
                <div className="form-container glass-box">
                    <div className="form">
                        <h2>Contact Me</h2>
                        <form action="#" id="contact-form" method="POST">
                            <div className="inputBx">
                                <input
                                    style={{
                                        border:
                                            this.state.error &&
                                            this.state.error.includes("name")
                                                ? "solid rgba(255, 33, 33, 0.7) 3px"
                                                : null,
                                    }}
                                    name="name"
                                    type="text"
                                    className="glass-box"
                                    placeholder="Full Name"
                                    value={this.state.name}
                                    onChange={(e) =>
                                        this.setState({ name: e.target.value })
                                    }
                                />
                            </div>
                            <div className="inputBx">
                                <input
                                    style={{
                                        border:
                                            this.state.error &&
                                            this.state.error.includes("subject")
                                                ? "solid rgba(255, 33, 33, 0.7) 3px"
                                                : null,
                                    }}
                                    name="title"
                                    type="text"
                                    className="glass-box"
                                    placeholder="Subject"
                                    value={this.state.subject}
                                    onChange={(e) =>
                                        this.setState({
                                            subject: e.target.value,
                                        })
                                    }
                                />
                            </div>
                            <div className="inputBx">
                                <input
                                    style={{
                                        border:
                                            this.state.error &&
                                            this.state.error.includes("e-mail")
                                                ? "solid rgba(255, 33, 33, 0.7) 3px"
                                                : null,
                                    }}
                                    name="email"
                                    type="email"
                                    className="glass-box"
                                    placeholder="Email"
                                    value={this.state.email}
                                    onChange={(e) =>
                                        this.setState({ email: e.target.value })
                                    }
                                />
                            </div>
                            <div className="inputBx">
                                <textarea
                                    style={{
                                        border:
                                            this.state.error &&
                                            this.state.error.includes("message")
                                                ? "solid rgba(255, 33, 33, 0.7) 3px"
                                                : null,
                                    }}
                                    name="message"
                                    className="glass-box"
                                    placeholder="Type Message Here..."
                                    value={this.state.message}
                                    onChange={(e) =>
                                        this.setState({
                                            message: e.target.value,
                                        })
                                    }
                                ></textarea>
                            </div>
                            <div className="glass-box inputBx">
                                <input
                                    type="submit"
                                    onClick={(e) => this.handleFormSubmit(e)}
                                    value="Send"
                                />
                            </div>
                        </form>
                        {this.state.mailSent ? (
                            <div className="glass-box accepted contact-form-submission-text">
                                The contact form has been submitted!
                            </div>
                        ) : null}
                        {this.state.error ? (
                            <div className="glass-box declined contact-form-submission-text">
                                {this.state.error}
                            </div>
                        ) : null}
                        {this.state.loading ? (
                            <div className="glass-box contact-form-submission-text">
                                Loading...
                            </div>
                        ) : null}
                    </div>
                </div>
            </div>
        );
    }
}

export default Contact;
