import React from "react";
import { Link } from "react-router-dom";

import NavigationLink from "./NavigationLink";

export default function Pagebar() {
    return (
        <nav className="glass-box navigation-bar">
            <Link
                to="/"
                className="navigation-logo fill-container white-text transparent-button"
            >
                Merlijn Sevenhuijsen
            </Link>
            <ul className="navigation-ul">
                <NavigationLink
                    associatedPage=""
                    pageName="Home"
                    pageColor="#193225"
                />
                <NavigationLink
                    associatedPage="About"
                    pageName="About"
                    pageColor="#3f48cc"
                />
                <NavigationLink
                    associatedPage="Projects"
                    pageName="Projects"
                    pageColor="#ea8aba"
                />
                <NavigationLink
                    associatedPage="Contact"
                    pageName="Contact"
                    pageColor="#e26f2e"
                />
                <li className="navigation-container">
                    <button className="rgb-button login-button transparent-button white-text">
                        <span>Log in</span>
                    </button>
                </li>
            </ul>
            <div className="burger">
                <div className="line1"></div>
                <div className="line2"></div>
                <div className="line3"></div>
            </div>
        </nav>
    );
}
